import { useState } from "react";
const Tab =props =>{
      const [tab1, settab1] = useState("");
      const [tab2, settab2] = useState("");

      const tab1handler = () => {
        settab1(
            <div>
                hii
            </div>
        );
        console.log(tab1);
      };
      const tab2handler = () => {
        settab2(<div>this is second tab</div>);
      };
    return <div>
        <button class="tablinks" onClick={tab1handler}>
              Paris
            </button>
            <button class="tablinks" onClick={tab2handler}>
              Tokyo
            </button>
    </div>

}

export default Tab ;