import { Fragment } from 'react';
import  '../Header/Header.css'
// import { NavDropdown,Nav,Container,Navbar } from "react-bootstrap";
import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import NavDropdown from "react-bootstrap/NavDropdown";
import logo from "../image/logo.png"

const Header = (props) =>{
    return (
      <Fragment>
        <div className="container-fluid header">
          <div className="container">
            <Navbar collapseOnSelect expand="lg">
              <Navbar.Brand href="#home">
                <img src={logo} className="logo"></img>
              </Navbar.Brand>
              <Navbar.Toggle />
              <Navbar.Collapse>
                <Nav className="mr-auto">
                  <Nav.Link href="#foo">HOME</Nav.Link>
                  <Nav.Link href="#bar">MENU SPECIAL</Nav.Link>
                  <NavDropdown
                    title="TODAYS SPECIAL"
                    id="collasible-nav-dropdown"
                  >
                    <NavDropdown.Item href="#action/1">PIZZA</NavDropdown.Item>
                    <NavDropdown.Item href="#action/2">BURGER</NavDropdown.Item>
                    <NavDropdown.Item href="#action/3">
                      FRENCH FRIES
                    </NavDropdown.Item>
                    <NavDropdown.Divider />
                    <NavDropdown.Item href="#action/4">NEWS</NavDropdown.Item>
                  </NavDropdown>
                </Nav>
              </Navbar.Collapse>
            </Navbar>
          </div>
        </div>
      </Fragment>
    );
}

export default Header;